.requestDetails {
  box-sizing: border-box;
  display: block;
  background-color: white;
  width: 100%;
  height: 100%;
  overflow-y: visible;
  padding: 10px;
}
.close {
  float: right;
  margin-left: 10px;
}
.title {
  font-size: 120%;
  padding: 5px;
}
.category {
  padding: 5px;
  margin-bottom: 1px;
  font-size: 120%;
  border-bottom: 1px #d8d8d8 solid;
}
.none {
  padding: 5px;
}
.params {
  width: 100%;
}
.params tr td:first-child {
  width: 150px;
}



