.lockTable {
  width: 100%;
  table-layout: fixed;
  padding-left: 10px;
  padding-right: 10px;
}
.lockTable th {
  border-bottom: 1px solid #ddd;
}
.lockTable td,
.lockTable th {
  vertical-align: top;
  padding: 7px 5px;
  width: 75px;
}
.lockTable .path,
.lockTable .query {
  width: auto;
}
.lockTable .time {
  width: 110px;
}
.lockTable .path {
  overflow-x: visible;
}
.lockTable .event {
  width: 100px;
}
.lockTable .type {
  text-align: right;
  width: 100px;
  padding-right: 10px;
}
.lockTable .query {
  overflow-x: hidden;
  text-overflow: ellipsis;
}
.lockTable .duration {
  width: 60px;
  text-align: right;
}
.lockTable .error {
  background-color: red;
}
.lockTable.hidden {
  overflow: hidden;
}
.info {
  position: relative;
  left: -80px;
}
.req-0 {
  background-color: #eee;
}
.req-1 {
  background-color: #fff;
}



